<template>

    <div class="form-group">
        <label :class="['form-group-label', {'active': active}]">
            <input :type="type"
                   class="form-control"
                   :name="name"
                   :value="value"
                   v-mask="mask"
                   :class="[classes]"
                   :placeholder="datepicker ? 'ДД.ММ.ГГГГ' : placeholder"
                   @input="$emit('input', $event.target.value)"
                   v-on="listeners">
            <span class="form-label">{{label}}</span>
            <span v-if="datepicker"
                  class="datepicker-icon">
                <span class="icon-calendar"></span>
            </span>
        </label>
    </div>

</template>

<script>
    export default {
        props: {
            value: [String, Number],
            mask: [String, Object],
            label: String,
            type: {
                type: String,
                default: 'text'
            },
            name: String,
            classes: [String, Object],
            placeholder: [String, Number],
            datepicker: Boolean,
        },
        data() {
            return {
                active: true
            }
        },
        methods: {
            checkActiveInput() {
                this.value.toString().trim().length > 0 ? this.active = true : this.active = false
            },
            updateValue (evt) {
                let value = evt.target.value
                this.$emit('input', value)
                this.checkActiveInput()
            },
            onFocus (value) {
                this.$emit('focus', value)
                this.checkActiveInput()
            },
            onBlur (value) {
                this.$emit('blur', value)
                setTimeout(() => {
                    this.checkActiveInput()
                }, 50)
            }
        },
        mounted() {
            this.checkActiveInput()
        },
        computed: {
            listeners() {
                return {
                    ...this.$listeners,
                    input: this.updateValue,
                    focus: this.onFocus,
                    blur: this.onBlur
                }
            }
        }
    }
</script>
