import axios from "axios";

let state = () => ({
    founders: [],
    foundersFinish: []
});

let actions = {
    async getFounders(ctx) {
        await axios ({
            method: 'get',
            url: '/constitutors'
        }).then(resp => {
            ctx.commit('updateFounders', resp.data.items)
        })
    },
    async sendMessageForFounder(ctx, founder) {
        let type = 1
        let data = {}
        founder.load = true
        if ( founder.entity ) {
            data.email = founder.email
            type = 2
        }
        else {
            data.phone = founder.phone.replace(/[^0-9]/g, '')
            type = 1
        }
        if ( type === 1 ) {
            await new Promise((resolve, reject) => {
                axios({
                    method: 'post',
                    url: `will/${founder.id}/send`,
                    data
                }).then(resp => {
                    founder.load = false
                    founder.status = 2
                    resolve()
                }).catch(reject)
            })
        }
        else {
            await new Promise((resolve, reject) => {
                axios({
                    method: 'post',
                    url: `will-ul/${founder.id}/send`,
                    data
                }).then(resp => {
                    founder.load = false
                    founder.status = 2
                    resolve()
                }).catch(reject)
            })
        }
    },
    async sendMessageForFounderFinish(ctx, dataSend) {
        let type = 1
        let data = {}
        dataSend.founder.load = true
        if ( dataSend.founder.entity ) {
            data.email = dataSend.founder.email
            type = 2
        }
        else {
            data.phone = dataSend.founder.phone.replace(/[^0-9]/g, '')
            type = 1
        }
        if ( type === 1 ) {
            await new Promise((resolve, reject) => {
                axios({
                    method: 'post',
                    url: `cons-will/${dataSend.id}/${dataSend.founder.id}/send`,
                    data
                }).then(resp => {
                    ctx.commit('updateFounderStatus', {id: dataSend.founder.id, status: 2})
                    dataSend.founder.load = false
                    dataSend.founder.status = 2
                    resolve()
                }).catch(reject)
            })
        }
        else {
            await new Promise((resolve, reject) => {
                axios({
                    method: 'post',
                    url: `cons-will-ul/${dataSend.id}/${dataSend.founder.id}/send`,
                    data
                }).then(resp => {
                    ctx.commit('updateFounderStatus', {id: dataSend.founder.id, status: 2})
                    dataSend.founder.load = false
                    dataSend.founder.status = 2
                    resolve()
                }).catch(reject)
            })
        }
    },
    async getFoundersFinish(ctx, id) {
        await axios({
            url: `/cons-wills/${id}`,
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('updateFoundersFinish', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    }
}

let mutations = {
    updateFounderStatus (state, data) {
        state.foundersFinish.filter(item => item.id === data.id)[0].sign.status = data.status
    },
    updateFounders(state, founders) {
        let res = founders
        res.forEach(item => item.load = false)
        state.founders = res
    },
    updateFoundersFinish(state, founders) {
        let res = founders
        res.forEach(item => item.load = false)
        state.foundersFinish = res
    }
}

export default {
    state,
    actions,
    mutations
}
