<template>

    <header class="header">

        <div class="header-row">

            <div class="header-logo">
                <a href="/" class="small-logo" @click.prevent="$router.push('/').catch(() => {})">
                    <img src="@/assets/images/dist/logo-small.svg" alt="">
                </a>
            </div>

            <div class="header-menu">
                <a href="#" class="active">Кредит для бизнеса</a>
                <a href="#">Банковская гарантия</a>
            </div>

            <div class="header-phone">
                <a href="tel:8 800 77-518-77"><span class="icon-chip"></span>8 800 77-518-77</a>
            </div>

            <div v-if="access"
                 :class="['header-notifications', {'active': notificationsOpened}]">
                <a href="#"
                   class="header-notifications-link"
                   @click.prevent="notificationsOpened = true">
                    <span v-if="notifications.noread !== undefined ? ( notifications.noread.length !== 0 ? true : false ) : false"
                          class="qnt" />
                    <span class="icon-bell" />
                </a>
                <div class="header-notifications-close"
                     @click="notificationsOpened = false">
                    <span class="icon-close-2"></span>
                </div>
                <div class="notifications-dropdown">
                    <Notifications @close-notifications="notificationsOpened = false"/>
                </div>
            </div>

            <div v-if="access"
                 class="header-company">
                <div class="company-name">{{company.name}}</div>
            </div>

            <div :class="['header-user', userDropdown ? 'opened' : '']">
                <a href="#"
                   class="header-user-name"
                   @click.prevent.stop="toggleUserMenu()">
                    <template v-if="( currentUser.name && currentUser.surname ) && company.ul !== 0">
                        {{currentUser.name}} {{currentUser.surname}}
                    </template>
                    <template v-else>
                        <span class="icon-user-acc"></span>
                    </template>
                </a>
                <div class="header-user-dropdown"
                     @click.prevent.stop="''"
                >
                    <div v-if="access"
                         class="header-user-links">
                        <div class="header-user-link" @click="userDropdown = false">
                            <router-link to="/users">Редактировать профиль</router-link>
                        </div>
                        <div class="header-user-link" @click="userDropdown = false">
                            <router-link to="/users">Добавить пользователя</router-link>
                        </div>
                    </div>
                    <div class="header-logout">
                        <a href="#"
                           class="logout-link"
                           @click="$router.push('/login')">Выход</a>
                    </div>
                </div>
            </div>

            <div class="header-menu-btn">
                <span @click.stop="mobMenuOpened = true"></span>
            </div>

        </div>

        <div :class="['bg-layout', {'active': notificationsOpened}]"
             @click="notificationsOpened = false"></div>

        <div class="mob-menu"
             :class="{'active': mobMenuOpened}"
             @click.stop="''">
            <div class="mob-menu-container">

                <div class="mob-menu-close"
                     @click="mobMenuOpened = false">
                    <span class="icon-close-3"></span>
                </div>

                <div class="mob-menu-logo">
                    <a href="/" class="small-logo" @click.prevent="$router.push('/')">
                        <img src="@/assets/images/dist/logo-small.svg" alt="">
                    </a>
                </div>

                <div class="mob-menu-user">{{currentUser.name}} {{currentUser.surname}}</div>

                <div class="mob-menu-phone">
                    <div class="header-phone">
                        <a href="tel:8 800 77-518-77"><span class="icon-chip"></span>8 800 77-518-77</a>
                    </div>
                </div>

                <div v-if="access"
                     class="mob-menu-company">{{company.name}}</div>

                <nav v-if="access"
                     class="mob-menu-nav">
                    <div class="mob-menu-nav-li"
                         @click="mobMenuOpened = false">
                        <router-link
                            v-for="(link, index) in menu"
                            :key="index"
                            :to="link.to"
                            v-slot="{ href, isActive, navigate }">
                            <a :href="href"
                               :class="['mob-menu-nav-link', {'active' : isActive, 'notification' : link.notification}]"
                               @click="navigate">
                                <span class="mob-menu-nav-icon"><span :class="link.icon"></span></span>
                                <span class="mob-menu-nav-name">{{link.title}}</span>
                            </a>
                        </router-link>
                    </div>
                </nav>

                <div class="mob-menu-account-nav">
                    <div v-if="access"
                         class="header-user-links">
                        <div class="header-user-link"
                             @click="mobMenuOpened = false">
                            <router-link to="/users">Редактировать профиль</router-link>
                        </div>
                        <div class="header-user-link"
                             @click="mobMenuOpened = false">
                            <router-link to="/users">Добавить пользователя</router-link>
                        </div>
                    </div>
                    <div class="header-logout">
                        <router-link to="/login" class="logout-link">Выход</router-link>
                    </div>
                </div>

                <div class="mob-menu-footer-links">
                    <a href="#" class="active">Кредит для бизнеса</a>
                    <a href="#">Банковская гарантия</a>
                </div>

            </div>
        </div>

    </header>

</template>

<script>
    import Notifications from "@/components/Blocks/Notifications";
    import {mapState} from "vuex";

    export default {
        name: 'Header',
        components: {Notifications},
        computed: {
            ...mapState({
                menu: state => state.menu.menu,
                currentUser: state => state.users.currentUser,
                company: state => state.users.company,
                access: state => state.users.access,
                notifications: state => state.notifications.notifications,
            }),
        },
        data: () => ({
            userDropdown: false,
            notificationsOpened: false,
            mobMenuOpened: false
        }),
        methods: {
            toggleUserMenu() {
                this.userDropdown = !this.userDropdown
            }
        },
        created() {
            const clickOutDropdown = () => {
                this.userDropdown = false
                this.mobMenuOpened = false
            }
            document.addEventListener('click', clickOutDropdown);
            this.$on('hook:beforeDestroy', () => document.removeEventListener('click', clickOutDropdown));
        },
    }
</script>
