import axios from 'axios'
import router from "@/router";

let state = () => ({
    authResPhone: '',
    authResCode: '',
    key: '',
    regPhone: '',
    regInn: '',
    regKey: '',
    regCode: '',
    innIsReg: false
})

let actions = {
    async getXSRF() {
        await axios.get('/csrf-cookie')
    },
    getKey(ctx, phone) {
        return new Promise((resolve, reject) => {
            axios({
                method: 'post',
                url: '/auth/login',
                data: {
                    phone: phone.replace(/[^0-9]/g, '')
                }
            }).then(resp => {
                if ( resp.data.status ) {
                    ctx.commit('setAuthResPhone', phone)
                    ctx.commit('setKey', resp.data.items.key)
                    resolve(true)
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    resolve(false)
                }
            }).catch(reject)
        })
    },
    getCode(ctx) {
        return new Promise((resolve, reject) => {
            axios.get('/csrf-cookie').then(() => {
                axios({
                    method: 'post',
                    url: '/auth/code',
                    data: {
                        code: ctx.state.authResCode,
                        key: ctx.state.key
                    }
                }).then(resp => {
                    if ( resp.data.status ) {
                        resolve(true)
                    }
                    else {
                        ctx.dispatch('getCodeText', resp.data.code)
                        resolve(false)
                    }
                }).catch(reject)
            })
        })
    },
    getAuth({dispatch}) {
        return new Promise((resolve, reject) => {
            dispatch('getCurrentUser', null, {root: true}).then(() => {
                resolve()
            }).catch(code => {
                if (code == 401) {
                    reject()
                }
            })
        })
    },
    getRegKey(ctx, data) {
        return new Promise((resolve, reject) => {
            let sendData = {
                inn: data.inn,
                phone: data.phone.replace(/[^0-9]/g, ''),
            }
            axios({
                method: 'post',
                url: '/auth/registration',
                data: sendData
            }).then(resp => {
                if ( resp.data.status ) {
                    ctx.commit('setRegData', sendData)
                    ctx.commit('setRegKey', resp.data.items.key)
                    resolve(true)
                }
                else {
                    resp.data.code === 107 ? ctx.commit('setInnRegBool', true) : ctx.dispatch('getCodeText', resp.data.code)
                    resolve(false)
                }
            }).catch(reject)
        })
    },
    getRegCode(ctx) {
        return new Promise((resolve, reject) => {
            axios({
                method: 'post',
                url: '/auth/regcode',
                data: {
                    code: ctx.state.regCode,
                    key: ctx.state.regKey
                }
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve(true)
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    resolve(false)
                }
            }).catch(reject)
        })
    },
    async restoration(ctx) {
        await axios({
            url: '/auth/restoration',
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                window.location.href = resp.data.items.url
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    logout() {
        axios({
            url: '/auth/logout',
            method: 'post'
        }).then(() => {
            router.push('/login')
        })
    }
}

let mutations = {
    setAuthResPhone(state, value) {
        state.authResPhone = value
    },
    setAuthResCode(state, value) {
        state.authResCode = value
    },
    setKey(state, value) {
        state.key = value
    },
    setRegData(state, data) {
        state.regPhone = data.phone.replace(/[^0-9]/g, '')
        state.regInn = data.inn
    },
    setRegKey(state, value) {
        state.regKey = value
    },
    setRegCode(state, value) {
        state.regCode = value
    },
    setInnRegBool(state, value) {
        state.innIsReg = value
    }
}

export default {
    state,
    actions,
    mutations
}
