import axios from "axios";

let state = () => ({
    errorCodeText: '',
    errorModal: false
})

let actions = {
    async getCodeText(ctx, code) {
        await axios({
            method: 'get',
            url: '/codes'
        }).then(resp => {
            ctx.commit('updateCode', resp.data[code])
        })
    },
}

let mutations = {
    updateCode(state, text) {
        state.errorCodeText = text
        state.errorModal = true
    },
    closeError(state) {
        state.errorModal = false
    }
}

export default {
    state,
    actions,
    mutations
}
