<template>
    <div id="container">

        <template v-if="componentLoaded">
            <component :is="layout">

                <router-view/>

            </component>
        </template>

        <Popup v-if="errorModal"
               :error="true"
               :classes="'error-modal'"
               @close-modal="closeErrorModal">

            <div class="popup-title">
                <div class="h3">Ошибка</div>
            </div>

            <div class="popup-body">
                <p class="error-text">{{errorCodeText}}</p>
                <button class="btn btn-lg btn-block close-modal">Все понятно</button>
            </div>

        </Popup>

    </div>
</template>


<script>
    import AuthLayout from '@/layouts/AuthLayout'
    import MainLayout from '@/layouts/MainLayout'
    import EmptyLayout from '@/layouts/EmptyLayout'
    import Popup from '@/components/Blocks/Popup'
    import {mapState} from 'vuex'
    import cookie from 'vue-cookies'

    export default {
        components: {
            AuthLayout,
            MainLayout,
            EmptyLayout,
            Popup
        },
        data: () => ({
            componentLoaded: false
        }),
        computed: {
            layout() {
                return (this.$route.meta.layout || 'main') + '-layout'
            },
            ...mapState({
                errorCodeText: state => state.errors.errorCodeText,
                errorModal: state => state.errors.errorModal
            })
        },
        methods: {
            closeErrorModal() {
                this.$store.commit('closeError')
            }
        },
        watch: {
            $route() {
                this.componentLoaded = true
            }
        }
    }
</script>
