import axios from 'axios'
import Vue from 'vue'
const fileDownload = require('js-file-download')

let state = {
    orderId: '',
    file: ''
}

let mutations = {
    setOrderId(state, id) {
        state.orderId = id
    }
}

let actions = {
    sendFile(ctx, data) {
        let sendPromises = [];
        ctx.dispatch('setLoadedForFiles', {id: data.id, value: true})
        data.files.forEach(fileItem => {
            let filePromise = new Promise((resolve, reject) => {
                let formData = new FormData();
                formData.append('doc', fileItem)
                axios({
                    url: `/doc/${data.id}/upload`,
                    method: 'post',
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    },
                    data: formData
                }).then(resp => {
                    if ( resp.data.status ) {
                        ctx.dispatch('setFileInfo', {
                            docsListId: data.id,
                            resp: resp.data.items
                        }).then(() => {
                            resolve()
                        })
                    }
                    else {
                        ctx.dispatch('getCodeText', resp.data.code)
                        resolve()
                    }

                }).catch(() => {
                    reject()
                })
            });
            sendPromises.push(filePromise)
        });
        Promise.all(sendPromises).then(() => {
            ctx.dispatch('setLoadedForFiles', {id: data.id, value: false})
        })
    },
    setLoadedForFiles({rootState}, data) {
        rootState.createOrder.initialDocuments.forEach((item, index) => {
            item.id === data.id ? Vue.set(rootState.createOrder.initialDocuments, index, {...item, loaded: data.value}) : null
        })
        rootState.createOrder.neededToLoadFiles.forEach((item, index) => {
            item.id === data.id ? Vue.set(rootState.createOrder.neededToLoadFiles, index, {...item, loaded: data.value}) : null
        })
    },
    setFileInfo({rootState}, data) {
        rootState.createOrder.initialDocuments.forEach(item => {
            if ( item.id === data.docsListId ) {
                item.files.push(data.resp)
            }
        })
        rootState.createOrder.neededToLoadFiles.forEach(item => {
            if ( item.id === data.docsListId ) {
                item.files.push(data.resp)
            }
        })
    },
    deleteFile({rootState}, id) {
        rootState.createOrder.initialDocuments.forEach((item, index) => {
            if ( item.id === id ) {
                item.files = []
            }
        })
    },
    async deleteFiles(ctx, id) {
        await axios({
            url: `/doc/${id}/clear`,
            method: 'delete'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.dispatch('deleteFile', id);
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    getFile(ctx, id) {
        return new Promise((resolve, reject) => {
            axios({
                url: `/file/${id}/download`,
                responseType: 'blob',
                method: 'get'
            }).then(resp => {
                if ( !resp.data.status ) {
                    resolve(resp.data)
                }
                else {
                    resolve()
                }
            }).catch(reject)
        })
    },
    getFileWithoutToken(ctx, data) {
        return new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data.token}`},
                url: `/file/${data.fileId}/download`,
                responseType: 'blob',
                method: 'get'
            }).then(resp => {
                if ( !resp.data.status ) {
                    resolve(resp.data)
                }
                else {
                    resolve()
                }
            }).catch(reject)
        })
    },
    async downloadFile(ctx, file) {
        await fileDownload(file.content, `${file.name}.${file.format}`)
    }
}

export default {
    state,
    mutations,
    actions
}
