<template>

    <div class="popup-wrapper"
         :class="opened ? 'opened' : ''"
         :style="{'display': visible ? 'block' : 'none', zIndex}">
        <div class="popup-flex">

            <div class="popup-block" :class="classes">
                <div @click="closePopup"
                     class="popup-close"><span class="icon-close"></span></div>
                <div class="popup-content">
                    <slot></slot>
                </div>
            </div>

            <div @click="closePopup"
                 class="popup-bg"></div>

        </div>
    </div>

</template>

<script>
    export default {
        name: "Popup",
        props: {
            classes: [String, Object, Array],
            error: {
                type: Boolean,
                default: false
            },
            zIndex: Number
        },
        data: () => ({
            visible: false,
            opened: false,
        }),
        methods: {
            closePopup() {
                this.opened = false
                setTimeout(() => {
                    this.visible = false
                    this.$emit('close-modal')
                }, 400)
            }

        },
        mounted() {
            let rootThis = this
            this.visible = true
            setTimeout(() => {
                this.opened = true
            }, 10)
            function escClick(e) {
                if ( (e.key === 'Escape' || e.key === 'Esc') ) {
                    rootThis.closePopup()
                }
            }
            document.addEventListener('keydown', e => escClick(e))
            this.$el.querySelectorAll('.close-modal').forEach(link => {
                link.addEventListener('click', rootThis.closePopup)
            })
            this.$on('hook:beforeDestroy', () => {
                this.$el.querySelectorAll('.close-modal').forEach(link => {
                    link.removeEventListener('click', rootThis.closePopup)
                })
                document.removeEventListener('keydown', e => escClick(e))
            })
        }
    }
</script>

