import axios from "axios";
import Vue from 'vue'

const resetValue = {
    orderId: '',
    creditName: '',
    creditSum: {
        min: 500000,
        max: 100000000,
        value: 500000
    },
    creditTarget: 'Пополнение оборотных средств',
    creditTerm: {
        min: 3,
        max: 120,
        value: 6
    },
    creditLine: false,
    creditInsureLife: true,
    orderStep: 0,
    orderFullInfo: {},
    visibleDocs: [],
    invisibleDocs: [],
    neededToLoadFiles: [],
    documentsForSigning: [],
    taxationSystems: [
        {
            value: 1,
            title: 'ОСН'
        },
        {
            value: 2,
            title: 'УСН'
        },
        {
            value: 3,
            title: 'ЕСХН'
        },
        {
            value: 4,
            title: 'ПСН'
        },
        {
            value: 5,
            title: 'НПД'
        },
    ],
    initialDocuments: [],
    questionnaire: '',
    documentsForSigningFinish: []
}

const state = JSON.parse(JSON.stringify(resetValue))

const mutations = {
    setOrderId(state, value) {
        state.orderId = value
    },
    setCreditSum(state, value) {
        if ( value === '' ) {
            value = '0'
        }
        state.creditSum.value = value
    },
    setCreditTarget(state, value) {
        state.creditTarget = value
    },
    setCreditTerm(state, value) {
        state.creditTerm.value = value
    },
    setOrderStepTo(state, index) {
        state.orderStep = index
    },
    setOrderStepPlus(state) {
        state.orderStep++
    },
    setOrderStepMinus(state) {
        state.orderStep--
    },
    setCreditInsureLife(state, value) {
        state.creditInsureLife = value
    },
    setCreditLine(state, value) {
        state.creditLine = value
    },
    resetStateParameters(state) {
        state.creditSum.value = resetValue.creditSum.value
        state.creditSum.min = resetValue.creditSum.min
        state.creditSum.max = resetValue.creditSum.max
        state.creditTarget = resetValue.creditTarget
        state.creditTerm.min = resetValue.creditTerm.min
        state.creditTerm.max= resetValue.creditTerm.max
        state.creditTerm.value = resetValue.creditTerm.value
        state.creditInsureLife = resetValue.creditInsureLife
        state.creditLine = resetValue.creditLine
    },
    updateCreditInsureLife(state, value) {
        state.creditInsureLife = value
    },
    updateCreditLine(state, value) {
        state.creditLine = value
    },
    setVisibleDocs(state, value) {
        state.visibleDocs = value
    },
    setInvisibleDocs(state, value) {
        state.invisibleDocs = value
    },
    setNeededToLoadFiles(state, value) {
        state.neededToLoadFiles = value
    },
    setDocumentsForSigning(state, value) {
        state.documentsForSigning = value
    },
    setDocumentsForSigningFinish(state, value) {
        state.documentsForSigningFinish = value
    },
    setQuestionnaire(state, file) {
        state.questionnaire = file
    },
    setiInitialDocuments(state, file) {
        state.initialDocuments = file
    },
    addressMatchesChange(state) {
        let index
        let arr = state.orderFullInfo.address.filter(address => address.type === 1)[0]
        state.orderFullInfo.address.forEach((address, i) => {
            if (address.type === 2) {
                index = i
            }
        })
        Vue.set(state.orderFullInfo.address, index, Object.assign({}, arr))
    }
}

const actions = {
    createOrder(ctx, tariff_id) {
        return new Promise((resolve, reject) => {
            axios({
                url: 'order/create',
                method: 'post',
                data: {
                    tariff_id
                }
            }).then(resp => {
                ctx.dispatch('updateOrder', resp.data.items.id)
                resolve(resp.data.items.id)
            }).catch(reject)
        })
    },
    async updateOrder(ctx, id) {
        await axios({
            url: `order/${id}/update`,
            method: 'post',
            data: {
                name: ctx.state.creditName,
                sum: ctx.state.creditSum.value.replace(/\s+/g, ''),
                term: ctx.state.creditTerm.value,
                line: ctx.state.creditLine,
                purpose: ctx.state.creditTarget,
                insurance: ctx.state.creditInsureLife ? 1 : 0
            }
        }).then(resp => {
            if ( !resp.data.status ) {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    getCurrentOrder(ctx, id) {
        return new Promise((resolve, reject) => {
            axios({
                url: `/order/${id}`,
                type: 'get'
            }).then(resp => {
                let order = resp.data.items

                if (resp.data.status) {

                    ctx.state.creditName = order.order.name
                    ctx.state.creditSum.value = order.order.sum
                    ctx.state.creditTarget = order.order.purpose
                    ctx.state.creditTerm.value = order.order.term
                    ctx.state.creditInsureLife = order.order.insurance === 1 ? true : false
                    ctx.state.creditLine = order.order.line === 1 ? true : false
                    ctx.state.orderStep = order.order.step

                    if ( order.order.taxationSystem === null ) order.order.taxationSystem = ctx.state.taxationSystems[0].value

                    let shareArr = [
                        {
                            type: 1,
                            row: 1,
                            title: '',
                            share: '',
                            term: false,
                        },
                        {
                            type: 1,
                            row: 2,
                            title: '',
                            share: '',
                            term: false,
                        },
                        {
                            type: 1,
                            row: 3,
                            title: '',
                            share: '',
                            term: false,
                        },
                        {
                            type: 2,
                            row: 1,
                            title: '',
                            share: '',
                            term: false,
                        },
                        {
                            type: 2,
                            row: 2,
                            title: '',
                            share: '',
                            term: false,
                        },
                        {
                            type: 2,
                            row: 3,
                            title: '',
                            share: '',
                            term: false,
                        },
                        {
                            type: 3,
                            row: 1,
                            title: '',
                            share: '',
                            term: false,
                        },
                        {
                            type: 3,
                            row: 2,
                            title: '',
                            share: '',
                            term: false,
                        },
                        {
                            type: 3,
                            row: 3,
                            title: '',
                            share: '',
                            term: false,
                        }
                    ]

                    if ( order.order.share === undefined ) {
                        order.order.share = shareArr
                    }
                    else if ( order.order.share.length === 0 ) {
                        order.order.share = shareArr
                    }
                    else {
                        order.order.share.forEach(shareItem => {
                            shareItem.term === 1 ? shareItem.term = true : shareItem.term = false
                        })
                    }
                    ctx.state.orderFullInfo = order
                }
                else {
                    if ( resp.data.code == 302 ) {
                        ctx.dispatch('getCodeText', resp.data.code)
                        resolve('error')
                    }
                    ctx.commit('resetStateParameters')
                }
                resolve(resp.data)
            }).catch(reject)
        })
    },
    async updateOrderOnlyOne(ctx, data) {
        await axios({
            url: `order/${data.id}/update`,
            method: 'post',
            data: data.fields
        }).then(resp => {
            if ( !resp.data.status ) {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    nextStep(ctx) {
        return new Promise((resolve, reject) => {
            axios({
                url: `/order/${ctx.state.orderId}/step/${ctx.state.orderStep + 1}`,
                method: 'get',
            }).then(resp => {
                if ( !resp.data.status ) {
                    ctx.dispatch('getCodeText', resp.data.code).then(resolve)
                }
                if ( ctx.state.orderStep === 9 ) {
                    ctx.dispatch('getFinalDocsList', ctx.state.orderId).then(() => {
                        ctx.commit('setOrderStepPlus');
                        resolve();
                    })
                }
                else {
                    ctx.commit('setOrderStepPlus');
                }
            }).catch(reject)
        })
    },
    async updateStep(ctx, step) {
        ctx.commit('setOrderStepTo', step);
        await axios({
            url: `/order/${ctx.state.orderId}/step/${ctx.state.orderStep}`,
            method: 'get',
        }).then(resp => {
            if ( !resp.data.status ) {
                ctx.dispatch('getCodeText', resp.data.code)
            }
            if ( ctx.state.orderStep === 10 ) {
                ctx.dispatch('getFinalDocsList', ctx.state.orderId)
            }
        })

    },
    async prevStep(ctx) {
        ctx.commit('setOrderStepMinus');
        await axios({
            url: `/order/${ctx.state.orderId}/step/${ctx.state.orderStep}`,
            method: 'get',
        }).then(resp => {
            if ( !resp.data.status ) {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    getDocsList(ctx, id) {
        let promises = []
        promises.push(new Promise((resolve, reject) => {
            axios({
                url: `order/${id}/docs/4`,
                method: 'get',
            }).then(resp => {
                if ( resp.data.status ) {
                    ctx.commit('setInvisibleDocs', resp.data.items)
                    resolve()
                }
                else {``
                    ctx.dispatch('getCodeText', resp.data.code)
                    reject()
                }
            }).catch(reject)
        }))
        promises.push(new Promise((resolve, reject) => {
            axios({
                url: `order/${id}/docs/5`,
                method: 'get',
            }).then(resp => {
                if ( resp.data.status ) {
                    resp.data.items.forEach(doc => {
                        doc.files.forEach(file => {
                            file.familiarized = false
                        })
                    })
                    ctx.commit('setVisibleDocs', resp.data.items)
                    resolve()
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    reject()
                }
            }).catch(reject)
        }))
        return Promise.all(promises)
    },
    async getNeededToLoadFiles(ctx, id) {
        await axios({
            url: `order/${id}/docs/6`,
            method: 'get',
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setNeededToLoadFiles', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getDocumentsForSigning(ctx, id) {
        await axios({
            url: `order/${id}/docs/uploaded`,
            method: 'get',
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setDocumentsForSigning', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getDocumentsForSigningFinish(ctx, id) {
        await axios({
            url: `order/${id}/docs/7`,
            method: 'get',
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setDocumentsForSigningFinish', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getOrderDocs(ctx, data) {
        await axios({
            url: `/order/${data.id}/docs/${data.type}`,
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                if ( data.type === 4 ) {
                    ctx.commit('setiInitialDocuments', resp.data.items)
                }
                if ( data.type === 5 ) {
                    ctx.commit('setQuestionnaire', resp.data.items)
                }
                if ( data.type === 6 ) {
                    ctx.commit('setQuestionnaire', resp.data.items)
                }
                if ( data.type === 7 ) {
                    ctx.commit('setQuestionnaire', resp.data.items)
                }
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    }
}


export default {
    state,
    mutations,
    actions
}
