import axios from "axios";

let state = {
    orders: [],
    ordersFinished: [],
    orderDocs: []
}

let actions = {
    async getOrders(ctx) {
        await axios({
            url: 'orders',
            type: 'get'
        }).then(resp => ctx.commit('updateOrders', resp.data.items))
    },
    async getOrdersFinished(ctx) {
        await axios({
            url: '/orders-finish',
            type: 'get'
        }).then(resp => {
            ctx.commit('updateOrdersFinished', resp.data.items)
        })
    },
    async getDetailOrderDocs(ctx, id) {
        await axios({
            url: `/order/${id}/docs`,
            type: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setOrderDocs', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    }
}

let mutations = {
    updateOrders(state, list) {
        state.orders = list
    },
    updateOrdersFinished(state, list) {
        state.ordersFinished = list
    },
    setOrderDocs(state, list) {
        state.orderDocs = list
    }
}

export default {
    state,
    actions,
    mutations
}
