import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store/index'
import cookie from "vue-cookies";

Vue.use(VueRouter)

const canCheckOrderPage = (to, from, next) => {
    let id = to.params.id
    store.dispatch('getCurrentOrder', id).then(res => {
        if ( res === 'error' ) {
            next('/')
        }
        else {
            let status = store.state.createOrder.orderFullInfo.order.status
            status > 1 ? next() : next('/')
        }
    }).catch(() => {
        next('/')
    })
}

