const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    scrollBehavior() {
        return {
            x: 0,
            y: 0,
            behavior: 'auto'
        };
    },
    routes
})

router.beforeEach(async (to, from, next) => {
    const notRequireAuth = to.matched.some(record => record.meta.notAuth)
    const notRequireToken = to.matched.some(record => record.meta.notToken)
    if (!notRequireToken) {
        if ( cookie.get('XSRF-TOKEN') === null ) {
            await store.dispatch('getXSRF')
        }
    }
    if (!notRequireAuth) {
        store.dispatch('getAuth').then(() => {
            if (store.state.users.access) {
                if (to.name != 'Create Order') {
                    store.commit('resetStateParameters')
                    store.commit('setOrderStepTo', 1)
                }
                if (to.name == 'Confirm Admin') {
                    next('/')
                }
            } else if (to.name !== 'Confirm Admin') {
                next('/confirm-admin')
            }
            next()
        }).catch(() => {
            next('/login')
        })
    } else {
        next()
        if (to.name === 'Login' || to.name === 'Registration') {
            store.dispatch('logout')
        }
    }
})

export default router
