import axios from 'axios'

let state = {
    notifications: []
}

let actions = {
    async getNotifications(ctx) {
        await axios({
            url: '/notifications',
            type: 'get'
        }).then(resp => {
            ctx.commit('setNotifications', resp.data.items)
        })
    },
    async readNotification(ctx, id) {
        await axios({
            url: `notification/${id}/scan`,
            type: 'get'
        })
    }
}

let mutations = {
    setNotifications(state, list) {
        state.notifications = list
    }
}

export default {
    state,
    actions,
    mutations
}
