<template>

    <div class="notifications-dropdown-content">

        <div class="notifications">

            <div class="h6">Новые</div>

            <div v-if="notifications.noread !== undefined ? ( notifications.noread.length !== 0 ? false : true ) : false"
                 class="notifications-empty">Нет новых уведомлений
            </div>

            <div v-else
                 class="notifications-list">
                <div v-for="notification in notifications.noread"
                     :key="notification.id"
                     class="notification-item new"
                     @mouseenter="readNotification(notification.id)">
                    <template v-if="notification.type === 1">
                        <div class="notification-title">{{getStatusText(notification).title}}</div>
                        <div class="notification-link">
                            <a :href="getStatusText(notification).link.url">{{getStatusText(notification).link.title}}</a>
                        </div>
                    </template>
                    <template v-else-if="notification.type === 2">
                        <div class="notification-title danger">{{notification.name}}</div>
                        <p>{{notification.items.text}}</p>
                    </template>
                    <template v-else-if="notification.type === 3">
                        <div class="notification-title">{{notification.name}}</div>
                        <p>{{notification.items.text}}</p>
                    </template>
                    <div class="notification-date">{{parseDate(notification.date)}}</div>
                </div>
            </div>

        </div>

        <div class="notifications">

            <div class="h6">Просмотренные</div>

            <div v-if="notifications.read !== undefined ? ( notifications.read.length !== 0 ? false : true ) : false"
                 class="notifications-empty">Нет прочитанных уведомлений
            </div>

            <div v-else
                 class="notifications-list">
                <div v-for="notification in notifications.read"
                     :key="notification.id"
                     class="notification-item">
                    <template v-if="notification.type === 1">
                        <div class="notification-title">{{getStatusText(notification).title}}</div>
                        <div class="notification-link">
                            <a :href="getStatusText(notification).link.url">{{getStatusText(notification).link.title}}</a>
                        </div>
                    </template>
                    <template v-else-if="notification.type === 2">
                        <div class="notification-title danger">{{notification.name}}</div>
                        <p>{{notification.items.text}}</p>
                    </template>
                    <template v-else-if="notification.type === 3">
                        <div class="notification-title">{{notification.name}}</div>
                        <p>{{notification.items.text}}</p>
                    </template>
                    <div class="notification-date">{{parseDate(notification.date)}}</div>
                </div>
            </div>

        </div>

    </div>

</template>

<script>

    import {mapState} from 'vuex'
    import moment from 'moment'

    export default {
        name: "Notifications",
        computed: {
            ...mapState({
                notifications: state => state.notifications.notifications
            })
        },
        methods: {
            getStatusText(item) {
                let status = item.items.status
                switch (status) {
                    case 1:
                        return {
                            title: `Кредит «${item.name}» - заявка создана`,
                            link: {
                                title: 'Перейти к заявкам',
                                url: `/orders`
                            }
                        }
                    case 2:
                        return {
                            title: `Кредит «${item.name}» - идет обработка данных`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 3:
                        return {
                            title: `Кредит «${item.name}» - ошибка отправки в диадок`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 4:
                        return {
                            title: `Кредит «${item.name}» - отправлен в диадок`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 5:
                        return {
                            title: `Кредит «${item.name}» - ошибка отправки в банк`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 6:
                        return {
                            title: `Кредит «${item.name}» - рассматривается банком`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 7:
                        return {
                            title: `Кредит «${item.name}» - отклонен`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 8:
                        return {
                            title: `Кредит «${item.name}» - требуются дополнительные документы`,
                            link: {
                                title: 'Загрузить документы',
                                url: `/load-documents/${item.items.order_id}`
                            }
                        }
                    case 9:
                        return {
                            title: `Кредит «${item.name}» - одобрен`,
                            link: {
                                title: 'Перейти к заявкам',
                                url: `/orders`
                            }
                        }
                    case 10:
                        return {
                            title: `Кредит «${item.name}» - отклонен пользователем`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 11:
                        return {
                            title: `Кредит «${item.name}» - резервирование расчетного счета`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 12:
                        return {
                            title: `Кредит «${item.name}» - есть зарезервированный расчетный счет`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 13:
                        return {
                            title: `Кредит «${item.name}» - подготовка документов`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 14:
                        return {
                            title: `Кредит «${item.name}» - подписание`,
                            link: {
                                title: 'Подписать заявку',
                                url: `/signing-documents/${item.items.order_id}`
                            }
                        }
                    case 15:
                        return {
                            title: `Кредит «${item.name}» - идет обработка данных`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 16:
                        return {
                            title: `Кредит «${item.name}» - ошибка отправки в диадок`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 17:
                        return {
                            title: `Кредит «${item.name}» - отправлен в диадок`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 18:
                        return {
                            title: `Кредит «${item.name}» - ошибка отправки в банк`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 19:
                        return {
                            title: `Кредит «${item.name}» - проверка документов`,
                            link: {
                                title: 'Просмотреть заявку',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 20:
                        return {
                            title: `Кредит «${item.name}» - кредит выдан`,
                            link: {
                                title: 'Просмотреть данные о кредите',
                                url: `/received-loans/${item.items.order_id}`
                            }
                        }
                }
            },
            parseDate(date) {
                let dateMoment = moment(date).locale('ru'),
                    currDate = moment(new Date()),
                    returnDate = dateMoment.format('DD MMMM');
                if ( currDate.year() > dateMoment.year() ) {
                    returnDate = dateMoment.format('DD MMMM YYYY') + ' года'
                }
                if ( currDate.day() === dateMoment.day() ) {
                    returnDate = `Сегодня в ${dateMoment.format('HH:mm')}`
                }
                return returnDate
            },
            readNotification(id) {
                this.$store.dispatch('readNotification', id)
            }
        },
        mounted() {
            this.$store.dispatch('getNotifications')
        },
    }
</script>

