import axios from 'axios'

let state = () => ({
    step: 1,
    confirm: true,
    person: {},
    creditTitle: '',
    esiaData: {},
    passport: {},
    key: '',
})

let mutations = {
    setAdminConfirm(state, value) {
        state.confirm = value
    },
    setAdminData(state, value) {
        state.person = value
    },
    setAdminEsiaData(state, value) {
        state.esiaData = value
    },
    setAdminAgreementKey(state, value) {
        state.key = value
    }
}

let actions = {
    async sendMessageForAdmin(ctx, phone) {
        await new Promise((resolve, reject) => {
            axios({
                method: 'post',
                url: `admreg/send`,
                data: {
                    phone: phone.replace(/[^0-9]/g, '')
                }
            }).then(resp => {
                if ( !resp.data.status ) {
                    ctx.dispatch('getCodeText', resp.data.code)
                    resolve(false)
                }
                else {
                    resolve(true)
                }
            }).catch(reject)
        })
    },
    async getAdminData(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data}`},
            url: '/admreg',
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setAdminData', resp.data.items)
                ctx.commit('setAdminEsiaData', resp.data.items.esia)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async signInEsiaAdmin(ctx, data) {
        await new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data}`},
                url: '/admreg/url',
                method: 'get'
            }).then(resp => {
                if ( resp.data.status ) {
                    window.location.href = resp.data.items.url
                    resolve()
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    reject()
                }
            }).catch(reject)
        })
    },
    async getAdminSubscribeCode(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data}`},
            url: '/admreg/subscribe',
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setAdminAgreementKey', resp.data.items.key)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getAdminSubscribeSuccess(ctx, data) {
        await new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data.token}`},
                url: '/admreg/code',
                method: 'post',
                data: {
                    code: data.code,
                    key: ctx.state.key
                }
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve()
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    reject()
                }
            }).catch(reject)
        })
    },
    async declineAdminAgreement(ctx, data) {
        await new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data}`},
                url: '/admreg/decline',
                method: 'get'
            }).then(resp => {
                if (resp.data.status) {
                    ctx.commit('setAdminConfirm', false)
                    resolve()
                } else {
                    ctx.dispatch('getCodeText', resp.data.code)
                }
            }).catch(reject)
        })
    },
}

export default {
    state,
    mutations,
    actions
}
