import axios from 'axios'

let state = () => ({
    step: 1,
    confirm: true,
    person: {},
    personUl: {},
    personFinish: {},
    creditTitle: '',
    esiaData: {},
    passport: {},
    key: '',
    docs: []
})

let mutations = {
    setConfirm(state, value) {
        state.confirm = value
    },
    setPersonData(state, value) {
        state.person = value
    },
    setPersonUlData(state, value) {
        state.personUl = value
    },
    setPersonDataFinish(state, value) {
        state.personFinish = value
    },
    setEsiaData(state, value) {
        state.esiaData = value
    },
    setAgreementKey(state, value) {
        state.key = value
    },
    setDocs(state, value) {
        state.docs = value
    }
}

let actions = {
    async getPersonData(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data}`},
            url: '/will',
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setPersonData', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getPersonUlData(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data}`},
            url: '/will-ul',
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setPersonUlData', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getPersonDataFinish(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data.token}`},
            url: `/cons-will/${data.orderId}`,
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setPersonDataFinish', resp.data.items)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        }).catch(err => {
            if ( err.response.status === 401 ) {
                document.querySelector('#container').innerHTML = 'Страница не актуальна!'
            }
        })
    },
    async signInEsia(ctx, data) {
        await new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data}`},
                url: '/will/url',
                method: 'get'
            }).then(resp => {
                if ( resp.data.status ) {
                    window.location.href = resp.data.items.url
                    resolve()
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    reject()
                }
            }).catch(reject)
        })
    },
    async signInEsiaFinish(ctx, data) {
        await new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data.token}`},
                url: `/cons-will/${data.orderId}/url`,
                method: 'get'
            }).then(resp => {
                if ( resp.data.status ) {
                    window.location.href = resp.data.items.url
                    resolve()
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    reject()
                }
            }).catch(reject)
        })
    },
    async getPassportData(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data.token}`},
            url: '/will',
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setEsiaData', resp.data.items.esia)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getPassportDataFinish(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data.token}`},
            url: `/cons-will/${data.orderId}`,
            method: 'get',
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setEsiaData', resp.data.items.esia)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getSubscribeCode(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data}`},
            url: '/will/subscribe',
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setAgreementKey', resp.data.items.key)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getSubscribeCodeFinish(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data.token}`},
            url: `/cons-will/${data.orderId}/subscribe`,
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.commit('setAgreementKey', resp.data.items.key)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getSubscribeSuccess(ctx, data) {
        await new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data.token}`},
                url: '/will/code',
                method: 'post',
                data: {
                    code: data.code,
                    key: ctx.state.key
                }
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve()
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    reject()
                }
            }).catch(reject)
        })
    },
    async getSubscribeSuccessFinish(ctx, data) {
        await new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data.token}`},
                url: `/cons-will/${data.id}/code`,
                method: 'post',
                data: {
                    code: data.code,
                    key: ctx.state.key
                }
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve()
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    reject()
                }
            }).catch(reject)
        })
    },
    async declineAgreement(ctx, data) {
        await new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data}`},
                url: '/will/decline',
                method: 'get'
            }).then(resp => {
                if (resp.data.status) {
                    ctx.commit('setConfirm', false)
                    resolve()
                } else {
                    ctx.dispatch('getCodeText', resp.data.code)
                }
            }).catch(reject)
        })
    },
    async declineAgreementFinish(ctx, data) {
        await new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data.token}`},
                url: `/cons-will/${data.id}/decline`,
                method: 'get'
            }).then(resp => {
                if (resp.data.status) {
                    ctx.commit('setConfirm', false)
                    resolve()
                } else {
                    ctx.dispatch('getCodeText', resp.data.code)
                }
            }).catch(reject)
        })
    },
    async getAgreementDocs(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data}`},
            url: 'will/docs',
            method: 'get'
        }).then(resp => {
            if (resp.data.status) {
                ctx.commit('setDocs', resp.data.items)
            } else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async getAgreementDocsFinish(ctx, data) {
        await axios({
            headers: {'Authorization': `Bearer ${data.token}`},
            url: `/cons-will/${data.orderId}/docs`,
            method: 'get'
        }).then(resp => {
            if (resp.data.status) {
                ctx.commit('setDocs', resp.data.items)
            } else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
}

export default {
    state,
    mutations,
    actions
}
