import axios from "axios"
import router from "@/router"
import cookie from "vue-cookies";

let state = {}

let actions = {
    importEsia(ctx, data) {
        axios({
            url: 'esia/import',
            method: 'post',
            data: {
                code: data.code,
                key: data.getParam
            }
        }).then(resp => {
            if ( resp.data.status ) {
                let type = resp.data.items.type
                let token = resp.data.items.token
                if ( type === 1 ) {
                    router.push(`/confirmation/signing?c=${token}`)
                }
                else if ( type === 2 ) {
                    router.push(`/agreement/signing?c=${token}`)
                }
                else if ( type === 3 ) {
                    router.push(`/agreement-finish/signing?c=${data.getParam}`)
                }
                else if ( type === 4 ) {
                    const token = resp.data.items.token
                    cookie.set('token', token)
                    axios.defaults.headers.common = {'Authorization': `Bearer ${token}`}
                    router.push('/')
                }
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    }
}

export default {
    state,
    actions
}
