<template>

    <div class="navbar" :class="[classes]">

        <div class="navbar-content">

            <div class="navbar-body">
                <nav class="navbar-nav">
                    <router-link
                        v-for="(link, index) in menu"
                        :key="index"
                        :to="link.to"
                        v-slot="{ href, isActive, navigate }">
                        <a :href="href"
                           :class="['navbar-nav-link', {'active notification' : isActive}]"
                           @click="navigate">
                            <span class="navbar-nav-link-icon"><span :class="link.icon"></span></span>
                            <span class="navbar-nav-link-name">{{link.title}}</span>
                        </a>
                    </router-link>
                </nav>
                <div class="navbar-toggle-arrow"
                     @click="changeToggleNavbar">
                    <span class="icon-double-arrow-right"></span>
                </div>
            </div>

        </div>
    </div>

</template>

<script>

    import {mapState} from 'vuex'

    export default {
        name: 'Navbar',
        props: {
            toggleNavbar: Boolean,
            classes: [Object, String]
        },
        computed: {
            ...mapState({
                menu: state => state.menu.menu,
                company: state => state.users.company
            })
        },
        methods: {
            changeToggleNavbar() {
                this.$store.commit('changeToggleNavbar')
            }
        }
    }
</script>
