import axios from "axios";

let state = {};

let actions = {
    sendSignature(ctx, data) {
        return new Promise((resolve, reject) => {
            axios({
                url: `/doc/${data.id}/signature`,
                method: 'post',
                data: {
                    doc: data.docs
                }
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve(true)
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    resolve()
                }
            }).catch(reject)
        })
    },
    sendSignatureForFounders(ctx, data) {
        return new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${data.token}`},
                url: `will-ul/${data.docId}/signature`,
                method: 'post',
                data: {
                    doc: data.docs
                }
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve(true)
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    resolve()
                }
            }).catch(reject)
        })
    },
    finishSignatureForFounders(ctx, token) {
        return new Promise((resolve, reject) => {
            axios({
                headers: {'Authorization': `Bearer ${token}`},
                url: `will-ul/send`,
                method: 'get',
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve(true)
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    resolve()
                }
            }).catch(reject)
        })
    },
    sendSignatureForFoundersFinish (ctx, data) {
        console.log(data)
        return new Promise(rootResolve => {
            let promises = []
            data.docs.forEach(doc => {
                promises.push(new Promise((resolve, reject) => {
                    axios({
                        headers: {'Authorization': `Bearer ${data.token}`},
                        url: `cons-will/${data.orderId}/${doc.docId}/signature`,
                        method: 'post',
                        data: {
                            doc: doc.files
                        }
                    }).then(resp => {
                        if ( resp.data.status ) {
                            resolve()
                        }
                        else {
                            ctx.dispatch('getCodeText', resp.data.code)
                            reject()
                        }
                    }).catch(reject)
                }))
            })
            Promise.all(promises).then(() => {
                axios({
                    headers: {'Authorization': `Bearer ${data.token}`},
                    url: `cons-will/${data.orderId}/send-contracts`,
                    method: 'get'
                }).then(resp => {
                    if ( !resp.data.status ) {
                        ctx.dispatch('getCodeText', resp.data.code)
                    }
                    else {
                        rootResolve()
                    }
                })
            })
        })
    },
}

export default {
    state,
    actions
}
