import axios from "axios";

let state = () => ({
    currentUser: {},
    company: {},
    users: [],
    access: 0
});

let actions = {
    getCurrentUser(ctx) {
        return new Promise((resolve, reject) => {
            axios ({
                method: 'get',
                url: '/user'
            }).then(resp => {
                if ( resp.data.status ) {
                    ctx.commit('updateCurrentUser', resp.data.items)
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                }
                resolve()
            }).catch(resp => {
                reject(resp.response.status)
                ctx.dispatch('logout')
            })
        })
    },
    async getUsers(ctx) {
        await axios ({
            method: 'get',
            url: '/users'
        }).then(resp => {
            console.log(resp.data.items)
            ctx.commit('updateUsers', resp.data.items)
        })
    },
    async updateUserInfo(ctx, user) {
        await axios({
            method: 'post',
            url: `/user/${user.id}/edit`,
            data: user
        }).then(resp => {
            if ( !resp.data.status ) {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async addUser(ctx, user) {
        await axios({
            method: 'post',
            url: `/user/create`,
            data: user
        }).then(resp => {
            if ( !resp.data.status ) {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async deleteUser(ctx, id) {
        await axios({
            method: 'delete',
            url: `/user/${id}`
        }).then(resp => {
            if ( !resp.data.status ) {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    }
}

let mutations = {
    updateCurrentUser(state, user) {
        state.currentUser = user.user
        state.company = user.company
        state.access = user.access
    },
    updateUsers(state, users) {
        state.users = users
    },
}

export default {
    state,
    actions,
    mutations
}
